import React, { useState } from 'react';
import { Container, Grid, IconButton, Button } from '@material-ui/core';
import { Link } from 'react-router-dom';
import ColumnsIcon from '@material-ui/icons/ViewModule';
import RowsIcon from '@material-ui/icons/ViewStream';

export interface INavProps {
  height?: number;
  onDirectionChanges: () => void;
}
export default function Nav({ height = 100, onDirectionChanges }: INavProps) {
  return (
    <nav
      style={{
        padding: 10,
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        backgroundColor: 'white',
      }}
    >
      <Container>
        <Grid container alignItems="center" spacing={0}>
          <Grid item>
            <img
              style={{ height: 30 }}
              src={process.env.PUBLIC_URL + '/favicon.png'}
              alt=""
            />
          </Grid>
          <Grid item>
            <Link to="es">
              <Button>ES</Button>
            </Link>
          </Grid>
          <Grid item>
            <Link to="pt">
              <Button>PT</Button>
            </Link>
          </Grid>
          <Grid item>
            <IconButton onClick={onDirectionChanges}>
              <ColumnsIcon style={{ display: 'block' }} />
            </IconButton>
          </Grid>
        </Grid>
      </Container>
    </nav>
  );
}
